.App {
    --header_height: 60px;

    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    padding: 0 30px;
}

.hw {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}

.hw::before {
    content: '';
    width: 100%;
    height: 1px;
    background-color: #D9D9D9;
    margin-bottom: 32px;
}

.hw::after {
    content: '';
    width: 100%;
    height: 1px;
    background-color: #D9D9D9;
    margin-top: 35px;
    margin-bottom: 60px;
}


.hwTitle {
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    font-weight: 600;
    color: #000000;

}